import React, { useEffect } from 'react'
import { Table } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import Loading from '../Loading'
import { emptyBasketAction } from '../reduxfiles/actions/basketActions'
import { getAllOrdersAction, sentOrderAction } from '../reduxfiles/actions/ordersActions'
import SomeThingWentWrong from '../SomeThingWentWrong'
import NavbarAdmin from './NavbarAdmin'

export default function Orders() {
    const getAllOrdersState = useSelector(state => state.getAllOrdersReducer)
    const { loading, error, allOrders } = getAllOrdersState

    const dispatch = useDispatch()

    useEffect(() => {
          dispatch(getAllOrdersAction())
    },[])

    function ordersent(id) {
          const order = {
                _id: id,
                isDeliverd: true,
          }
          console.log(order);
          dispatch(sentOrderAction(order))
    }
    return (
        <div className="page-container">
        <NavbarAdmin />
        <h4 className="users-header"> /:</h4>
                  <div className="homepage-menu shadow">
                        {loading && <Loading />}
                        {error && <SomeThingWentWrong />}
                        {allOrders && (
                                    <Table striped bordered hover responsive>
                                          <thead>
                                                <tr>
                                                      <th>#</th>
                                                      <th> </th>
                                                      <th> </th>
                                                      <th> </th>
                                                      <th> </th>
                                                      <th> </th>
                                                      <th> </th>
                                                      <th> </th>
                                                </tr>
                                          </thead>
                                          <tbody>
                                                {
                                                    allOrders.map((order, i) => (
                                                            <tr>
                                                                  <td>{i + 1}</td>
                                                                  <td>{
                                                                        order.serviceType === 'inner' ? ' ' :
                                                                              order.serviceType === 'out' ? ' ()' : ' ()'
                                                                  }</td>
                                                                  <td>{order.tableNum ? order.tableNum : '-'}</td>
                                                                  <td>{
                                                                        <div>
                                                                              <p>-{order.customer[0].customerName}</p>
                                                                              <p>-{order.customer[0].customerTel}</p>
                                                                              <p>-{order.customer[0].customerAddress}</p>
                                                                        </div>
                                                                  }</td>
                                                                  <td>
                                                                        {
                                                                              order.basketItems.map((item, i) => (
                                                                                    <div>
                                                                                          <h5>{item.name}</h5>
                                                                                          <p>{`${item.qty} * ${item.price} = ${item.fee}`}</p>
                                                                                          <hr />
                                                                                    </div>
                                                                              ))
                                                                        }
                                                                  </td>
                                                                  <td>{order.totalPrice}</td>
                                                                  <td>{order.orderDescriptions}</td>
                                                                  <td>
                                                                        {order.isDeliverd ? <button className='edit-btn' >  .</button> : <button onClick={() => ordersent(order._id)} className='delete-btn m-3'>  ...</button>}
                                                                        {!order.isDeliverd && <Link className="m-4" to={`/userorder/edit/${order._id}`}><i class="bi bi-pencil-fill" onClick={() => dispatch(emptyBasketAction())}></i></Link> }
                                                                  </td>
                                                            </tr>
                                                      ))
                                                }
                                          </tbody>
                                    </Table>
                              )
                        }
                  </div>
            </div>
    )
}
