import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import Loading from './Loading';
import { loginUserAction } from './reduxfiles/actions/userAction';

export default function LoginPage() {
    const [username, setusername] = useState('')
    const [password, setpassword] = useState('')
    const dispatch = useDispatch()
    const loginUserState = useSelector(state => state.loginUserReducer)
    const { loading, error } = loginUserState

    function goingToLogin() {
        if (username && password) {
            const user = {
                username,
                password
            }
            dispatch(loginUserAction(user))
        } else {
            alert('     .')
        }
    }

    return (
        <div className="page-container">
            <h3 className="login_header"> </h3>
            <div className='login-form shadow'>
                <label className="login-label-form">
                     :
                    <input type='text' placeholder=' ' className='login-inputs' onChange={(e) => setusername(e.target.value)} />
                </label>
                <label className="login-label-form">
                     :
                    <input type='password' placeholder=' ' className='login-inputs' onChange={(e) => setpassword(e.target.value)} />
                </label>
                <button onClick={goingToLogin} className='login-btn mb-3'></button>
                {loading && <Loading />}
                {error && <p className="empty-basket">     .</p>}
            </div>
        </div>
    )
}
