import React from 'react'
import { Link } from 'react-router-dom'

export default function NavbarAdmin() {
    return (
        <div className="component-navbar">
            <div className="component-navbar-selections">
                    <Link className="nav-links" to='/admin/users'></Link>
                    <Link className="nav-links" to='/admin/foods'></Link>
                    <Link className="nav-links" to='/admin/orders'></Link>
                    <Link className="nav-links" to='/admin/customers'></Link>
            </div>
        </div>
    )
}
