import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Loading from '../Loading'
import { addUserAction, getAllUsersAction } from '../reduxfiles/actions/userAction'
import SomeThingWentWrong from '../SomeThingWentWrong'
import NavbarAdmin from './NavbarAdmin'

export default function Adduser() {
      const dispatch = useDispatch()

      useEffect(() => {
            dispatch(getAllUsersAction())
      }, [])

      const getUsersState = useSelector(state => state.getAllUsersReducer)
      const { loading, error, users } = getUsersState

      const [username, setusername] = useState('')
      const [password, setpassword] = useState('')
      const [cpassword, setcpassword] = useState('')
      const [access, setaccess] = useState('simple')
      const [phoneNum, setphoneNum] = useState('')

      useEffect(() => {
            if (users.length > 0) {
                  setusername(users.length + 10001)
            }
      }, [users, loading, dispatch])

      function saveUser() {
            if (password === cpassword && username && password !== '' && phoneNum) {
                  const user = {
                        username,
                        password,
                        access,
                        phoneNum,
                        isActive: true,
                  }
                  dispatch(addUserAction(user))
            } else {
                  alert('       .')
            }
      }

      return (
            <div className="page-container">
                  <NavbarAdmin />
                  <h4 className="users-header"> // :</h4>
                  {
                        loading ? <Loading /> : error ? <SomeThingWentWrong /> : (
                              <div className="add-user-form">
                                    <label className="form-label">
                                           :
                                          <input type='text' className='add-food-form-input' placeholder=' ' value={username} />
                                    </label>
                                    <label className="form-label">
                                           :
                                          <input type='password' className='add-food-form-input' placeholder=' ' value={password} onChange={(e) => setpassword(e.target.value)} />
                                    </label>
                                    <label className="form-label">
                                            :
                                          <input type='password' className='add-food-form-input' placeholder='  ' value={cpassword} onChange={(e) => setcpassword(e.target.value)} />
                                    </label>
                                    <label className="form-label">
                                           :
                                          <select placeholder='' className='add-food-form-input' value={access} onChange={(e) => setaccess(e.target.value)} >
                                                <option value='admin'>()</option>
                                                <option value='cashier'></option>
                                                <option value='simple'></option>
                                          </select>
                                    </label>
                                    <label className="form-label">
                                            :
                                          <input type='tel' placeholder='  ' className='add-food-form-input' value={phoneNum} onChange={(e) => setphoneNum(e.target.value)} />
                                    </label>
                                    <button className='add-food-btn' onClick={saveUser}></button>
                              </div>
                        )
                  }
            </div>
      )
}
